.body {
  font-weight: 700;
  background-color: white;
  border-radius: 20px;
  margin: 150px auto;
  padding: 40px 40px;
  max-width: 85%;
  min-width: 65%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 83% 17%;
      grid-template-columns: 83% 17%;
}

.body__text {
  position: relative;
}

.body__info {
  padding: 20px;
  margin: 0px auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.languages {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  margin-bottom: 10px;
}

.languages__el:hover {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer;
  border: 6px #d2c1ff dashed;
}

.languages__el {
  margin: 5px 5px;
  max-width: 60px;
  max-height: 48px;
  border: 6px white outset;
}

.languages__el img {
  background-size: cover;
  width: 100%;
  height: 100%;
}

.languages__el_active {
  border: 6px #7388ff inset;
  margin: 5px 5px;
  max-width: 60px;
  max-height: 48px;
}

.languages__el_active img {
  background-size: cover;
  width: 100%;
  height: 100%;
}

.behindText {
  position: absolute;
  font-size: 21px;
  color: #c2c2c2;
  z-index: 0;
  padding: 6px 10px;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

.mainText {
  position: relative;
  font-weight: 700;
  width: auto;
  height: 100%;
  z-index: 2;
}

.mainText textarea {
  background: transparent;
  color: black;
  padding: 5px 9px;
  font-weight: 700;
  font-size: 21px;
  outline: none !important;
  border: 1px solid #8fc3ff;
  border-radius: 9px;
  width: 100%;
  height: 100%;
}

.mainText textarea:focus {
  border: 1px solid #5b06e4;
  -webkit-box-shadow: 0 0 6px #719ECE;
          box-shadow: 0 0 6px #719ECE;
}

.speed .accuracy {
  font-size: 24px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 5px 0px;
}

.indicators {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  padding: 5px 0px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.indicators__number {
  margin: 0px 0px 15px 0px;
  font-size: 22px;
  color: #7050ef;
}

.reset {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-top: 5px;
}

.reset button {
  border-radius: 16px;
  padding: 10px 20px;
  color: white;
  background-color: #7455ff;
  border: 0px;
}

.reset button:hover {
  color: white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  background-color: #8c73ff;
  border: 0px;
}

@media screen and (max-width: 1000px) {
  .body {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 100%;
        grid-template-columns: 100%;
  }
  .body__text {
    -ms-grid-row: 2;
    grid-row: 2;
    width: 100%;
  }
  .body__info {
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    width: 100%;
    -ms-grid-row: 1;
    grid-row: 1;
    background-color: #eeeeee;
    display: -webkit-box !important;
    display: -ms-flexbox !important;
    display: flex !important;
    -webkit-box-orient: horizontal !important;
    -webkit-box-direction: normal !important;
        -ms-flex-direction: row !important;
            flex-direction: row !important;
    border: 1px solid black;
    border-radius: 20px 20px 0px 0px;
  }
  .languages {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin-bottom: 10px;
  }
  .indicators {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  textarea {
    min-height: 200px;
  }
}
